/**
 * User
 */
export const getUser = async (userId) => {
  try {
    const res = await axiosInstance.get(`${baseUrl}/users/${userId}`);
    return res.data;
  } catch (error) {
    console.error('[Get User failed]: ', error);
    throw error.response.data.message;
  }
};

