/**
 * 
 */
export const putUserAccount = async ( {userId, payload} ) => {
  try {
    const res = await axiosInstance.put(`${baseUrl}/users/${userId}/account`, payload);
    return res.data;
  } catch (error) {
    console.error('[PUT UserAccount failed]: ', error);
    return error.response.data;
  }
};

