import AuthInputs from '../components/AuthInputs';
import ACLogo from '../assets/imgs/logo.png';
import Button from '../components/Button';
import { Link, useNavigate } from 'react-router-dom';
import { useContext, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { notifyContext } from '../contexts/NotifyContext';

const requiredDatas = [
  {
    title: '',
    placeholder: '',
    id: 0,
    disabled: false,
    invalid: false,
    errorMessage: ' ',
    textLimit: '',
    value: '',
  },
  {
    title: '',
    placeholder: '',
    id: 1,
    disabled: false,
    invalid: false,
    errorMessage: '',
    textLimit: 50,
    value: '',
  },
  {
    title: 'Email',
    placeholder: 'Email',
    id: 2,
    disabled: false,
    invalid: false,
    errorMessage: '',
    textLimit: '',
    value: '',
  },
  {
    title: '',
    placeholder: '',
    id: 3,
    disabled: false,
    invalid: false,
    errorMessage: '',
    textLimit: '',
    value: '',
  },
  {
    title: '',
    placeholder: '',
    id: 4,
    disabled: false,
    invalid: false,
    errorMessage: '',
    textLimit: '',
    value: '',
  },
];

const SignUpPage = () => {
  const [datas, setDatas] = useState(requiredDatas);
  const { showNotification } = useContext(notifyContext);
  const { register, login } = useAuth();
  const navigate = useNavigate();

  function handleInputChange({ id, value }) {
    const cloneDatas = [...datas];
    cloneDatas[id].invalid = false;
    cloneDatas[id].value = value;
    if (datas[1].value.length > 50) {
      cloneDatas[1].invalid = true;
      cloneDatas[1].errorMessage = '';
    }
    setDatas(cloneDatas);
  }

  async function handleRegistBtnClicked() {
    const cloneDatas = [...datas];
    for (const element of cloneDatas) {
      element.invalid = false;
    }

    var precheckError = false;
    for (const element of cloneDatas) {
      var hasEmpty = false;
      if (element.value.trim() === '') {
        element.invalid = true;
        element.errorMessage = '';
        precheckError = true;
        hasEmpty = true;
      }
      if (hasEmpty) {
        showNotification('error', '');
      }
    }

    if (datas[4].value !== datas[3].value) {
      cloneDatas[4].invalid = true;
      cloneDatas[4].errorMessage = '';
      showNotification('error', '');
      precheckError = true;
    }

    if (datas[1].value.length > 50) {
      cloneDatas[1].invalid = true;
      cloneDatas[1].errorMessage = '';
      showNotification('error', '');
      precheckError = true;
    }

    if (precheckError) {
      setDatas(cloneDatas);
      return;
    }

    const result = await register({
      account: datas[0].value,
      username: datas[1].value,
      email: datas[2].value,
      password: datas[3].value,
      checkPassword: datas[4].value,
    });

    if (result.status === 'success') {
      showNotification('success', ',');
      const loginResult = await login({
        account: datas[0].value,
        password: datas[3].value,
      });

      if (loginResult.status === 'success') {
        showNotification('success', '');
        navigate('/main');
      } else {
        showNotification('error', '');
      }

    } else if (result.message === 'email ') {
      cloneDatas[2].invalid = true;
      cloneDatas[2].errorMessage = 'email ';
      setDatas(cloneDatas);
      showNotification('error', 'email ');
      return;
    } else if (result.message === 'account ') {
      cloneDatas[0].invalid = true;
      cloneDatas[0].errorMessage = '';
      setDatas(cloneDatas);
      showNotification('error', '');
      return;
    } else if (result.message !== '') {
      alert(result.message);
    } else {
      alert('');
    }
  }

  function resetInvalid(id) {
    const cloneDatas = [...datas];
    cloneDatas[id].invalid = false;
    setDatas(cloneDatas);
  }

  return (
    <div className={`flex flex-col items-center`}>
      <img src={ACLogo} className={`w-[50px] h-[50px] mt-16`} alt="AC Logo" />
      <p className={`h-[33px] text-[#1C1C1C] heading-h3 text-center mt-6 mb-[32px]`}></p>
      <AuthInputs requiredDatas={datas} onChange={handleInputChange} />
      <Button onClick={handleRegistBtnClicked} text={''} width={`w-[356px]`} height={`h-[46px]`} />
      <div className={`w-[356px] flex justify-center	mt-4 py-1.5`}>
        <Link to="/login" className={`content-l-r text-primary underline underline-offset-2	`}>
          
        </Link>
      </div>
    </div>
  );
};
export default SignUpPage;
