import { Navbar, AuthInputs, Button } from '../components';
import { notifyContext } from '../contexts/NotifyContext';
import { useContext, useState } from 'react';
import { putUserAccount } from '../apis/user';

const AccountUpdate = () => {
  const userInfo = JSON.parse(localStorage.getItem('userInfo'));
  const [datas, setDatas] = useState([
    {
      title: '',
      placeholder: '',
      id: 1,
      disabled: false,
      invalid: false,
      errorMessage: ' ',
      textLimit: '',
      value: userInfo.account,
    },
    {
      title: '',
      placeholder: '',
      id: 2,
      disabled: false,
      invalid: false,
      errorMessage: '',
      textLimit: 50,
      value: userInfo.name,
    },
    {
      title: 'Email',
      placeholder: '',
      id: 3,
      disabled: false,
      invalid: false,
      errorMessage: '',
      textLimit: '',
      value: userInfo.email,
    },
    {
      title: '',
      placeholder: '',
      id: 4,
      disabled: false,
      invalid: false,
      errorMessage: '',
      textLimit: '',
      value: '',
    },
    {
      title: '',
      placeholder: '',
      id: 5,
      disabled: false,
      invalid: false,
      errorMessage: '',
      textLimit: '',
      value: '',
    },
  ]);
  
  const { showNotification } = useContext(notifyContext);
  const userId = userInfo.id;

  function handleInputChange({ id, value }) {
    const cloneDatas = [...datas];
    const findData = cloneDatas.find((element) => element.id === id);
    findData.invalid = false;
    findData.value = value;
    setDatas(cloneDatas);
  }

  async function handleSaveButtonClicked() {
    const cloneDatas = [...datas];
    for (const element of cloneDatas) {
      element.invalid = false;
    }

    var precheckError = false;
    for (const element of cloneDatas) {
      var hasEmpty = false;
      if (element.value.trim() === '') {
        element.invalid = true;
        element.errorMessage = '';
        precheckError = true;
        hasEmpty = true;
      }
      if (hasEmpty) {
        showNotification('error', '');
      }
    }

    if (datas[4].value !== datas[3].value) {
      cloneDatas[4].invalid = true;
      cloneDatas[4].errorMessage = '';
      showNotification('error', '');
      precheckError = true;
    }

    if (datas[1].value.length > datas[1].textLimit) {
      cloneDatas[1].invalid = true;
      cloneDatas[1].errorMessage = '';
      showNotification('error', '');
      precheckError = true;
    }

    if (precheckError) {
      setDatas(cloneDatas);
      return;
    }

    const result = await putUserAccount({
      userId: userId,
      payload: {
        account: datas[0].value,
        name: datas[1].value,
        email: datas[2].value,
        password: datas[3].value,
        checkPassword: datas[4].value,
      },
    });

    console.log(result)

    if (result.status === 'success') {
      userInfo.account = datas[0].value;
      userInfo.name = datas[1].value;
      userInfo.email = datas[2].value;
      localStorage.setItem('userInfo', JSON.stringify(userInfo));
      showNotification('success', '');
    } else if (result.message === 'email ') {
      cloneDatas[2].invalid = true;
      cloneDatas[2].errorMessage = 'email ';
      setDatas(cloneDatas);
      showNotification('error', 'email ');
      return;
    } else if (result.message === 'account ') {
      cloneDatas[0].invalid = true;
      cloneDatas[0].errorMessage = '';
      setDatas(cloneDatas);
      showNotification('error', '');
      return;
    } else if (result.message === '') {
      showNotification('error', '');
      setDatas(cloneDatas);
      return;
    } else if (result.message === '') {
      showNotification('error', '');
      setDatas(cloneDatas);
      return;
    } else if (result.message === '') {
      showNotification('error', '');
      setDatas(cloneDatas);
      return;
    } else if (result.message !== '') {
      alert(result.message);
    } else {
      alert('');
    }
  }

  return (
    <>
      <Navbar title={''} />
      <div className="p-6">
        <AuthInputs requiredDatas={datas} onChange={handleInputChange} />
      </div>
      <div className="px-6 pt-4 text-end">
        <Button text={''} onClick={handleSaveButtonClicked}></Button>
      </div>
    </>
  );
};
export default AccountUpdate;
